@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700&display=swap');
*, *::before, *::after{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
    /* max-width: 100%; */
    font-family: Roboto;
}

.leaderBoard{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
    background-color: lightgray;
    padding-top: 20px;
    padding-bottom: 20px;
    height: 100vh;
    width: 100vw;
}

.form{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 5px;
}

.form>button, .leaderBoard>button{
    width: 100px;
    height: 50px;
    font-size: 1rem;
    border-radius: 10px;
    border: none;
    opacity: 0.8;
    background-color: darkgray;
    font-weight: 500;
}

.form>button:hover, .leaderBoard>button:hover{
    opacity: 1;
}

input{
    text-align: center;
    width: 15vw;
    height: 5vw;
    font-size: 1.5rem;
    border: none;
    border-radius: 10px;
}

.topPlayers{
    background-color: lightcoral;
    width: 40vw;
    height: 15vw;
    border-radius: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
}

.clicked{
    background-color: darkgray;
    color: gray;
    pointer-events: none;
}

ol{
    display: flex;
    flex-direction: column;
    gap: 10px;
}

h3{
    text-align: center;
}